export default {
    name: "app-dialog",
    props: {
        title: {
            default: '',
        },
        content: {
            default: '',
        },
        visible: {
            type: Boolean,
            default: false,
        },
        confirmText: {
            default: 'чбошод',
        },
    },
    data() {
        return {
            iVisible: this.visible,
        };
    },
    watch: {
        visible(v) {
            this.iVisible = v;
        },
    },
    methods: {
        close() {
            this.iVisible = false;
            this.$emit('update:visible', this.iVisible);
        },
        confirm() {
            this.iVisible = false;
            this.$emit('update:visible', this.iVisible);
        },
    },
}
